import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Loading from "components/shared-components/Loading";
import { APP_PREFIX_PATH } from "configs/AppConfig";

export const AppViews = () => {
    return (
        <Suspense fallback={<Loading cover='content' />}>
            <Switch>
                <Route
                    path={`${APP_PREFIX_PATH}/home`}
                    component={lazy(() => import(`./home`))}
                />
                <Route
                    path={`${APP_PREFIX_PATH}/main/clients/settings/:id`}
                    component={lazy(() => import(`./pages/setting`))}
                />
                <Redirect
                    exact
                    from={`${APP_PREFIX_PATH}`}
                    to={`${APP_PREFIX_PATH}/home`}
                />
                <Route
                    exact
                    path={`${APP_PREFIX_PATH}/main/clients/list`}
                    component={lazy(() => import(`./pages/user-list`))}
                />
                <Route
                    exact
                    path={`${APP_PREFIX_PATH}/main/scheduler`}
                    component={lazy(() => import(`./pages/scheduler`))}
                />
            </Switch>
        </Suspense>
    );
};

export default React.memo(AppViews);
